
import Loading from 'vux/src/components/loading/index.vue'

import InfiniteLoading from 'vue-infinite-loading'
import api from '@/utils/api'
import MySearch from '@/components/MySearch'
import LiveItem from './pagelet/LiveItem'

export default {
name: 'LiveList',
components: {
Loading,
InfiniteLoading,
MySearch,
LiveItem
},
data () {
return {
searchTitle: '',
listData: [],
loading: '',
page: 1
}
},
methods: {
setData (data = []) {
this.listData.splice(0)
this.listData = data
},
async handleSearch (value) {
this.searchTitle = value
this.page = 1
this.fetchData()
},
async infiniteHandler ($state) {
try {
const data = await api.getLiveList(this.page++, this.searchTitle)
const len = data.length
if (len > 0) {
this.listData = this.listData.concat(data)
if (len < 10) {
$state.loaded()
} else {
$state.complete()
}
} else {
$state.complete()
}
} catch (err) {
console.error(err)
}
},
async fetchData () {
this.loading = true
try {
const data = await api.getLiveList(this.page++, this.searchTitle)
this.setData(data)
} catch (err) {
console.error(err)
}
this.loading = false
}
},
mounted () {
this.fetchData()
}
}



