
import dateFormat from 'vux/src/tools/date/format.js'

import pageConfig from '@/utils/pageConfig'

export default {
name: 'LiveItem',
props: {
live: {
type: Object,
default: {}
}
},
computed: {
link () {
return `${pageConfig.liveLink}?liveId=${this.live.id}`
},
createtime () {
return dateFormat(this.live.createtime * 1000, 'YYYY-MM-DD')
},
poster () {
return this.live.covermap
}
},
methods: {
formatStatus (status) {
switch (status) {
case '3':
return '结束回看'
case '2':
return '直播中...'
case '1':
return '未开始'
default:

}
}
}
}



